import {mapState,mapActions} from 'vuex';
import {hostUrl} from '@/http/request.js';
export default {
	props:{
		addCartorBuy:{
			type:Number,
			default:0
		}
	},
	data() {
		return {
			cloneImg: '@/static/images/detailImg/gbico.png',
			hostUrl:hostUrl,
			seleteUnitIndex:0,
			seleteNumber:1
		}
	},
	methods: {
		...mapActions(['getAddCart']),
		addCart(){
			// 验证登陆信息
			let userId = uni.getStorageSync('userId');
			if(!userId){
				uni.showToast({
					title:'请先登陆',
					icon:'none'
				})
				return 
			}
			let {id,unit,stock} = this.info;
			let {seleteNumber,seleteUnitIndex} = this;
			if(seleteNumber > stock){
				uni.showToast({
					title:'选择数量大于库存',
					icon:'none'
				})
				return
			}
			// 加入购物车
			let option = {
				id:id,
				userId:userId,
				quantity:seleteNumber,
				isChecked:1,
				unit:unit.split('/')[seleteUnitIndex]
			}
			this.getAddCart(option);
			this.$emit('clone')
		},
		clone() {
			this.$emit('clone')
		},
		move() {
			return
		},
		seleteUnit(index){
			this.seleteUnitIndex = index;
		},
		add(){
			if(this.seleteNumber > this.info.stock){
				uni.showToast({
					title:'购买数量超过库存',
					icon:'none',
					duration:1500
				})
				return 
			}
			this.seleteNumber = this.seleteNumber+1;
		},
		decrease(){
			if(this.seleteNumber <= 1){
				uni.showToast({
					title:'购买数量不能小于1',
					icon:'none',
					duration:1500
				})
				return 
			}
			this.seleteNumber = this.seleteNumber-1;
		},
		buy(){
			let userId = uni.getStorageSync('userId');
			if(!userId){
				uni.showToast({
					title:'请先登陆',
					icon:'none'
				})
				return 
			}
			let {id,unit,stock} = this.info;
			let {seleteNumber,seleteUnitIndex} = this;
			if(seleteNumber > stock){
				uni.showToast({
					title:'选择数量大于库存',
					icon:'none'
				})
				return
			}
			// 立即购买
			let option = {
				id:id,
				userId:userId,
				quantity:seleteNumber,
				isChecked:2,
				unit:unit.split('/')[seleteUnitIndex]
			}
		
			this.getAddCart(option)
			this.$emit('clone')
		}
	},
	computed:{
		...mapState({
			info:state => state.details.info
		}),
		unit_arr(){
			
			return this.info.unit.split('/')
		}
	}
}
